*{
  margin: 0px 0px;
  padding: 0px 0px;
  color : white;
}

body{
  background-color: black;
  display: inline;
  height : 100vh;
  width  : 100vw;
}

.outbox{
  display: flex; 
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  /* background-color: aqua;   */
  height : 100%;
  width  : 100%;

}

.inbox{
  display: flex; 
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin : 3vh;
  height: 25vh;
  width: 90%;
  background: url("./purple.jpg");
  /* background: url("./orange.jpg"); */
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 3vh;
}


.headingbox{
  /* background: red; */
  /* height: 5vh; */
  margin : 1vh;
  font-size: 6vh;
  text-align: center;    
}

.clockbox{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4vh;
  margin: 1vh 1vw;
}


.timeicon{
  height:4vh ;
}


.infobox{
  display: flex;
  margin : 1vh;
  margin-top: 0px;
  height: 10vh;
  width: 100%;
  justify-content:space-between ;
  align-items: center;
  /* background-color: blueviolet; */
}


.subicon,.venueicon, .teaicon{
  height: 4vh;
  margin-right: 4px;
}


.details{
  margin : 1vh 1vw;
  /* text-align: center; */
  font-size: 3vh;
  justify-self: start;
  display: flex;
  justify-content: center;
  align-items: center;
} 
 







#msgoutbox{
  display: flex; 
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  /* background-color: aqua;   */
  height : 100%;
  width  : 100%;

}



#msginbox{
  display: flex; 
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin : 3vh;
  height: 25vh;
  width: 90%;
  background: url("purple.jpg");
  /* background: url("./orange.jpg"); */
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 3vh;
}


#m_heading{
    font-size: 5vh;
    padding-bottom: 3vh;
}

#msg{
    font-size: 4vh;
    padding-bottom: 3vh;
}